<template>
	<div>
		<div class="restrict">
			<div class="row">
				<div class="maxTitle">
					<div>PEOPLE</div>
				</div>
				<div class="principal clearfix">
					<div class="imginfo left">
						<img :src="piImg" />
					</div>
					<div class="textinfo right">
						<div class="swiper-container" style="width:100%;">
							<div class="swiper-wrapper" style="width:100%;">
								<div class="swiper-slide">
									<div class="slide-inner">
										<div class="title">{{ piName }}<i class="people-ico">PI</i></div>
										<p>{{ content }}</p>
									</div>
								</div>
							</div>
							<div class="swiper-scrollbar"></div>
						</div>
					</div>
				</div>
			</div>
			<div class="peoplelist">
				<div class="peopleli row">
					<div class="flex-container">
						<div
							class="flex-item"
							v-for="(i, j) in people"
							:key="j"
						>
							<div>
								<img :src="i.src" class="txpic" />
							</div>
							<div class="title">
								{{ i.name }}<i class="people-ico">{{ i.degree }}</i>
							</div>
							<p class="text">
								{{ i.message }}
							</p>
							<a class="more" onclick="popups()" href="javascript:void(0);"
								>READ MORE</a
							>
						</div>
					</div>
				</div>
			</div>
			<div class="alumnibg">
				<div class="row">
					<div class="maxTitle">
						<div>ALUMNI</div>
					</div>
					<div class="alumni clearfix" v-for="(i, j) in alumni" :key="j">
						<div class="imginfo left">
							<img :src="i.src" style="width:468px;height:280px;" />
						</div>
						<div class="textinfo right">
							<div class="title">
								{{ i.name }}<i class="people-ico">{{ i.degree }}</i>
							</div>
							<p>{{ i.message }}</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>


<script>
import * as API from '@/api/video/';
export default {
	data: () => ({
		piImg:null,
		piName:null,
		content: "",
		people:[],
		alumni:[]

	}),
	methods: {
		popups: () => {},
		filterTransform: (arr, metric) => {
			return arr.filter(v => v.group === metric).map(v => {
				var degree = ''
				if(v.degree === 4) {
					degree = 'student'
				} else if(v.degree === 2) {
					degree = 'staff'
				}
				return {
					name: v.title,
					src: v.imgpath,
					degree: degree,
					message: v.emessage
				}
			})
		},
		load: function(){
			API.getPeopleList().then((res) => {
				var people_list = res.data.items;

				var leader = people_list.find(v => v.group === 1)
				this.piImg = leader.imgpath
				this.piName = leader.title
				this.content = leader.emessage

				this.people = this.filterTransform(people_list, 2)
				this.alumni = this.filterTransform(people_list, 3)
			});
		}
	},
	beforeMount() {
		this.load()
	}
}
</script>
<style>
	.flex-container {
		/* We first create a flex layout context */
		display: flex;

		/* Then we define the flow direction 
		and if we allow the items to wrap 
		* Remember this is the same as:
		* flex-direction: row;
		* flex-wrap: wrap;
		*/
		flex-flow: row wrap;
		width: 1200px;
		margin: 0 auto;
		/* Then we define how is distributed the remaining space */
		justify-content: space-start;
		list-style: none;
	}

	.flex-item {
		padding: 5px;
		width: 378px;
		margin-top: 10px;
		margin-right: 12px;
	}
	.txpic {
		width: 378px;
		height: 289px;
	}
</style>